import React from "react";
import "./FeaturedMovie.css";
import Tmdb from "../Tmdb";
const FeaturedMovie = ({ item, functionShowModal }) => {
  const year = new Date(item.first_air_date);
  //   let genres = [];
  //   for (let i in item.genres) {
  //     genres.push(item.genres[i].name);
  //   }
  const showInfo = async (item) => {
    const fullData = await Tmdb.getMovieInfo(item.id, item.first_air_date);
    // console.log(fullData);
    functionShowModal(fullData);
  };
  const limitedOverview = () => {
    const arrayOverview = item.overview.split(" ");

    if (arrayOverview.length > 60) {
      const newArray = arrayOverview.splice(0, 60);
      const newString = newArray.join(" ") + "...";

      return newString;
    }

    return item.overview;
  };
  return (
    <section
      className="featured"
      style={{
        backgroundImage: `url(https://image.tmdb.org/t/p/original${item.backdrop_path})`,
      }}
    >
      <div className="featured--vertical">
        <div className="featured--horizontal">
          <div className="featured--name">{item.name}</div>
          <div className="featured--info">
            <div className="featured--relevance">
              {item.vote_average * 10}% relevance
            </div>
            <div className="featured--seasons">
              {item.seasons.length} season{item.seasons.length > 1 ? "s" : ""}
            </div>

            <div className="featured--year">{year.getFullYear()}</div>
          </div>
          <div className="featured--description">{limitedOverview()}</div>
          <div className="featured--buttons">
            <a
              href="#"
              className="featured--btn-play"
              onClick={showInfo.bind(null, item)}
            >
              <i className="fas fa-play"></i>
              Play
            </a>
            <a href="#" className="featured--btn-list">
              <i className="fas fa-plus"></i>
              Add to List
            </a>
          </div>
          <div className="featured--genres">
            <strong>Genres:</strong>
            {item.genres.map((gen, key) => {
              if (key === item.genres.length - 1) {
                return gen.name;
              }
              return gen.name + ", ";
            })}
          </div>
        </div>
      </div>
    </section>
  );
};
export default FeaturedMovie;
