import "./MovieRow.css";
import { useState } from "react";
import NavigateBeforeIcon from "@material-ui/icons/NavigateBefore";
import NavigateNextIcon from "@material-ui/icons/NavigateNext";
import Tmdb from "../Tmdb";
export default ({ title, items, functionShowModal }) => {
  const [xValue, setXvalue] = useState(0);
  const showInfo = async (item) => {
    const fullData = await Tmdb.getMovieInfo(item.id, item.first_air_date);
    // console.log(fullData);
    functionShowModal(fullData);
  };

  const leftArrowHandler = () => {
    let x = xValue + Math.round(window.innerWidth / 2);
    if (x > 0) {
      x = 0;
    }
    setXvalue(x);
  };
  const rightArrowHandler = () => {
    let x = xValue - Math.round(window.innerWidth / 2);
    const movieListLenght = items.results.length * 300;
    if (window.innerWidth - movieListLenght > x) {
      x = window.innerWidth - movieListLenght - 60;
    }

    setXvalue(x);
  };
  return (
    <div className="movieRow">
      <h2>{title}</h2>
      <div className="left-arrow" onClick={leftArrowHandler}>
        <NavigateBeforeIcon style={{ fontSize: 50 }} />
      </div>

      <div className="right-arrow" onClick={rightArrowHandler}>
        <NavigateNextIcon style={{ fontSize: 50 }} />
      </div>
      <div className="movieRow--listarea">
        <div className="movieRow--list" style={{ marginLeft: xValue }}>
          {items.results.map((item, key) => {
            return (
              <div
                key={key}
                className="movieRow--item"
                onClick={showInfo.bind(null, item)}
              >
                <img
                  src={`https://image.tmdb.org/t/p/w300${item.poster_path}`}
                  alt={item.original_title}
                />
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
};
