import "./App.css";
import Tmdb from "./Tmdb";
import MovieRow from "./components/MovieRow";
import { useEffect, useState } from "react";
import FeaturedMovie from "./components/FeaturedMovie";
import Header from "./components/Header";
import Modal from "./components/Modal";
function App() {
  const [movieList, setMovieList] = useState([]);
  const [featuredMovie, setFeaturedMovie] = useState(null);
  const [blackHeader, setBlackHeader] = useState(false);
  const [showModal, setShowModal] = useState(false);
  const [modalItem, setModalItem] = useState(null);
  const modalCloseHandler = () => {
    setShowModal(false);
  };
  const showModalHandler = (item) => {
    setShowModal(true);
    setModalItem(item);
    console.log(item);
  };
  useEffect(() => {
    const getAllItems = async () => {
      const items = await Tmdb.getHomeList();
      setMovieList(items);

      //get featured movie
      const netflixOriginals = items[0].items.results;
      const randomNumber = Math.floor(
        Math.random() * (netflixOriginals.length - 1)
      );
      const itemSelected = netflixOriginals[randomNumber];

      const featuredMovie = await Tmdb.getMovieInfo(
        itemSelected.id,
        itemSelected.first_air_date
      );
      setFeaturedMovie(featuredMovie);
    };
    getAllItems();
  }, []);
  useEffect(() => {
    const scrollHandler = () => {
      if (window.scrollY > 50) {
        setBlackHeader(true);
      } else {
        setBlackHeader(false);
      }
    };
    window.addEventListener("scroll", scrollHandler);
    return () => {
      window.removeEventListener("scroll", scrollHandler);
    };
  }, []);
  return (
    <div className="page">
      <Header black={blackHeader} />
      {showModal && modalItem ? (
        <Modal item={modalItem} modalClose={modalCloseHandler} />
      ) : (
        ""
      )}
      {featuredMovie && (
        <FeaturedMovie
          item={featuredMovie}
          functionShowModal={showModalHandler}
        />
      )}
      <div className="lists">
        {movieList.map((item, key) => {
          return (
            <MovieRow
              key={key}
              title={item.title}
              items={item.items}
              functionShowModal={showModalHandler}
            />
          );
        })}
      </div>
      {movieList && featuredMovie ? (
        ""
      ) : (
        <div class="lds-ring">
          <div></div>
          <div></div>
          <div></div>
          <div></div>
        </div>
      )}
    </div>
  );
}

export default App;
