import React from "react";
import "./Modal.css";
const Modal = ({ item, modalClose }) => {
  const year = new Date(
    item.first_air_date ? item.first_air_date : item.release_date
  );
  console.log(item);
  return (
    <React.Fragment>
      <div className="backdrop" onClick={modalClose}></div>
      <div className="modal">
        <div className="close" onClick={modalClose}>
          <i className="far fa-times-circle"></i>
        </div>

        <div
          className="top-modal"
          style={{
            backgroundImage: `url(https://image.tmdb.org/t/p/original${item.backdrop_path})`,
          }}
        >
          <div className="details">
            <h1 className="name">{item.name}</h1>
            <div className="buttons">
              <a href="" className="btn-play">
                <i className="fas fa-play"></i>
                Play
              </a>
              <a href="" className="circle add">
                <i className="fas fa-plus"></i>
              </a>
              <a href="" className="circle like">
                <i className="far fa-thumbs-up"></i>
              </a>
              <a href="" className="circle dislike">
                <i className="far fa-thumbs-down"></i>
              </a>
            </div>
          </div>
        </div>
        <div className="bottom">
          <div className="info">
            <div className="info-top">
              <div className="relevance">
                {item.vote_average * 10}% relevance
              </div>
              {item.seasons && (
                <div className="seasons">
                  {item.seasons.length} season
                  {item.seasons.length > 1 ? "s" : ""}
                </div>
              )}
              <div className="year">{year.getFullYear()}</div>
            </div>

            <div className="description">{item.overview}</div>
          </div>
          <div className="genres">
            <strong>Genres:</strong>
            {item.genres.map((gen, key) => {
              if (key === item.genres.length - 1) {
                return gen.name;
              }
              return gen.name + ", ";
            })}
          </div>
        </div>
      </div>
    </React.Fragment>
  );
};

export default Modal;
