const API_BASE = "https://api.themoviedb.org/3";
const API_KEY = "f612c8f0d2888e87813fbd6f6db615ed";

const basicFetch = async (endpoint) => {
  const data = await fetch(`${API_BASE}${endpoint}`);
  const json = await data.json();
  return json;
};

export default {
  getHomeList: async () => {
    return [
      {
        slug: "originals",
        title: "Netflix Originals",
        items: await basicFetch(
          `/discover/tv?api_key=${API_KEY}&with_network=213`
        ),
      },
      {
        slug: "trending",
        title: "Recommeded for you",
        items: await basicFetch(`/trending/all/week?api_key=${API_KEY}`),
      },
      {
        slug: "toprated",
        title: "Toprated",
        items: await basicFetch(`/movie/top_rated?api_key=${API_KEY}`),
      },
      {
        slug: "action",
        title: "Action",
        items: await basicFetch(
          `/discover/movie?api_key=${API_KEY}&with_genres=28`
        ),
      },
      {
        slug: "comedy",
        title: "Comedy",
        items: await basicFetch(
          `/discover/movie?api_key=${API_KEY}&with_genres=35`
        ),
      },
      {
        slug: "horror",
        title: "Horror",
        items: await basicFetch(
          `/discover/movie?api_key=${API_KEY}&with_genres=27`
        ),
      },
      {
        slug: "romance",
        title: "Romance",
        items: await basicFetch(
          `/discover/movie?api_key=${API_KEY}&with_genres=10749`
        ),
      },
      {
        slug: "documentary",
        title: "Documentary",
        items: await basicFetch(
          `/discover/movie?api_key=${API_KEY}&with_genres=99`
        ),
      },
    ];
  },
  getMovieInfo: async (movieId, isSeries = false) => {
    let data = {};
    if (movieId) {
      if (isSeries) {
        data = await basicFetch(`/tv/${movieId}?api_key=${API_KEY}`);
      } else {
        data = await basicFetch(`/movie/${movieId}?api_key=${API_KEY}`);
      }
    }

    return data;
  },
};
